* {
    box-sizing: border-box;
}

html, body {
    position: relative;
    height: 100%;
    width: 100%;
    margin: 0;
    // overflow-x: hidden; // with this window.pageYOffset doesn't work in chrome
    // https://stackoverflow.com/a/30056532
}

p {
    font-size: calc(22px + (24 - 22) * (100vw - 425px) / (1024 - 425)) !important;
}

@media (max-width: 1024px) {
    p {
        font-size: 20px !important;
    }
    h2 {
        font-size: 50px !important;
    }
}
@media (max-width: 590px) {
    p {
        font-size: 18px !important;
    }
    h2 {
        font-size: 40px !important;
        line-height: 50px !important;
    }
}

li, a {
    margin: 0;
    text-decoration: none;
    list-style: none;
}

ul {
    padding: 0;
    margin: 0;
}

h1, h2, h3, h4, h5, p {
    margin: 0;
}

button {
    padding: 0;
    margin: 0;
    cursor: pointer;
}

button:focus {
    outline: none;
}

.wrapper {
    height: 100%;
}

.image3d {
    transform: perspective(50vmin) rotate(45deg) scale(1.0/$SQRT2);
    transform-style: preserve-3d;
    perspective: 50vmin;
}