.section__flight {
    height: 100vh;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
    -khtml-user-select: none; /* Konqueror HTML */
    -moz-user-select: none; /* Firefox */
    -ms-user-select: none; /* Internet Explorer/Edge */
    user-select: none; /* Non-prefixed version, currently supported by Chrome and Opera */
}
#flightBox {
    position: relative;
    width: 100%;
    height: 100vh;
    cursor: pointer;
}
#flightBox #flight-controller {
    width: 100%;
    height: 100%;
    border: none;
    outline: 0;
    z-index: 100;
}
#flightBox .flight-free-scroll {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    -webkit-transition-delay: .2s;
    transition-delay: .2s;
}
#flightBox .flight-free-scroll .popup-circle {
    background: rgba(0,0,0,0.6);
    width: 100vw;
    height: 100vw;
    min-height: 100vh;
    min-width: 100vh;
    border-radius: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) scale(1.5);
    transform: translate(-50%, -50%) scale(1.5);
    -webkit-transition-duration: .2s;
    transition-duration: .2s;
}
#flightBox .flight-free-scroll .text {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    color: white;
    font-size: 34pt;
    text-align: center;
    -webkit-transition-duration: .2s;
    transition-duration: .2s;
}
#flightBox.active .flight-free-scroll {
    pointer-events: none;
    visibility: hidden;
}
#flightBox.active .flight-free-scroll .popup-circle {
    transform: translate(-50%, -50%) scale(0);
}
#flightBox.active .flight-free-scroll .text {
    opacity: 0;
}
#flightBox.active .flight-stop{
    opacity: 1;
    transform: scale(1);
}
#flightBox.active .flight-free-scroll{
    pointer-events: none;
    visibility: hidden;
}

@media (max-width:590px) {
    #flightBox {
        cursor: none;
    }
}

@media (max-width:425px) {
    #flightBox .flight-free-scroll .text {
        font-size: 25px;
        width: 100%;
    }
}