
.top__header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    padding-top: 40px;
    z-index: 100;
    transform: .3s;
}
.header__color {
    transition-property: transform, opacity;
    padding: 22px 0 22px 0;
    background: rgba(0, 0, 0, 0.9);
    opacity: .93;
    z-index: 100;
    animation: showColorHeader 0.5s;
}
@keyframes showColorHeader {
    0% {
        opacity: 0;
        transform: translateY(-100%);
    }
    100% {
        opacity: 0.93;
        transform: translateY(0);
    }
}
.header__nav {
    padding: 0 20px;
    width: 100%;
}
.header__container {
    max-width: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end;
}
.header__menu {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end;
    &-item {
        margin-left: 35px;
        font-size: 20px;
        text-transform: uppercase;
        @include segeoRegular;
        border-bottom-width: 0%;
    }
}
.menu-item-link {
    position: relative;
    color: #fff;
    transition: .2s;
    transition-property: transform, opacity;
    &::before {
        content: '';
        position: absolute;
        width: 100%;
        transform: scale(0, 1);
        transform-origin: left center;
        height: 1px;
        background-color: #fff;
        bottom: -5px;
        transition: .2s;
        transition-property: transform, opacity;
        }
        &:hover::before {
            transform: scale(1);
        }
    &:hover {
        color: #e1e1e1;
    }
}
.menu__burger {
    display: none;
    position: relative;
    width: 55px;
    height: 25px;
    align-self: flex-end;
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #fff;
        transition: .3s;
        transition-property: transform, opacity;
        transform-origin: left center;
    }
    span {
        position: absolute;
        top: 50%;
        transform: translateY(-50%) scale(.6,1);
        width: 100%;
        height: 2px;
        background-color: #fff;
        transition: .3s;
        transition-property: transform, opacity;
        transform-origin: left center;
    }
    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: #fff;
        transform: scale(.3, 1);
        transition: .3s;
        transition-property: transform, opacity;
        transform-origin: left center;
    }
}
.menu__burger--active {
    &::before {
        transform: translateY(12.5px) rotate(45deg) scale(.8,1);
        transform-origin: center center;
    }
    span {
        transform: translateX(500px);
    }
    &::after {
        transform: translateY(-10px) rotate(-45deg) scale(.8,1);
        transform-origin: center center;
    }
}
.menu-item-icon {
    display: none;
}
.menu-item-line {
    display: none;
}

.header__menu-item {
    font-size: 20px;
}

@media (max-width: 790px) {
    .section__present {
        align-items: flex-start !important;
    }
    .present__container {
        flex-direction: column;
        text-align: center;
        justify-content: center !important;
        height: 80%;
    }
    .present__slogan-about {
        width: 100%;
        left: 50%;
        transform: translateX(-50%);
    }
}

@media (max-width: 590px) {
    .present__container {
        padding: 0 20px !important;
    }
    .top__header {
        position: fixed;
    }
    .menu__burger {
        display: block;
    }
    .header__container {
        justify-content: flex-end;
    }
    .menu__icon-box {
        position: relative;
        margin-right: 25px;
    }
    .menu-item-icon {
        position: relative;
        display: block;
    }
    .menu-item-line {
        display: block;
        position: absolute;
        top: 100%;
        left: 50%;
        width: 100%;
        text-align: center;
        transform: translate(-53%, 0);
        .line-img {
            object-fit: contain;
            object-position: center;
            max-height: 100px;
        }
    }
    .header__menu-item:last-child .menu-item-line  {
        display: none;
    }
    .header__nav {
        position: absolute;
        top: 0;
        left: 0;
        transform: translate(-100%, 0);
        height: 100vh;
        width: 55%;
        background: rgba(0, 0, 0, 0.9);
        padding: 0;
        transition: .3s;
        transition-property: transform, opacity;
    }
    .header__nav--active {
        z-index: 100;
        transform: translate(0);
    }
    .header__menu {
        display: flex;
        justify-content: center;
        flex-direction: column;
        padding-left: 37px;
        height: 100%;
        width: 100%;
    }
    .header__menu-item {
        margin-bottom: 80px;
        display: flex;
        align-items: center;
        margin-left: 0;
        &:last-child {
            margin-bottom: 0;
        }
    }
    .header__container {
        padding: 0 20px;
    }
    
}

@media(max-width: 425px) {
    .header__menu-item {
        font-size: 18px;
    }
    .header__color {
        padding: 16px 0 16px 0;
    }
    .menu__burger {
        width: 50px;
        height: 25px;
    }
}

@media (max-width: 375px) {
    .present__container {
        padding: 0 !important;
    }
    .header__nav {
        width: 60%;
    }
}

@media (max-width: 320px) {
    .header__nav {
        width: 70%;
    }
}