.video__container {
    max-width: 1400px;
    margin: 0 auto;
}
.section__video {
    padding: 90px 0 0 0;
}
.video__slogan {
    text-align: center;
}
.video__block-title {
    padding: 0 10px;
}
.slogan-title-caption {
    @include segeoRegular;
    font-size: 40px;
    line-height: 40px;
    color: #000;
    display: inline-block;
    margin-bottom: 20px;
}
.first-word {
    position: relative;
    &::before {
        content: url(../img/main/svg/quotes.svg);
        position: absolute;
        top: 50%;
        transform: translateY(-35%) scale(1);
        left: -45px;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: -1;
        opacity: 0;
        transition: $animTransition;
    }
}
.first-word._active::before {
    animation: animScaleQut 1s ease-in-out infinite;
    animation-iteration-count: 1;
    opacity: 1;
}
.last-word._active::after {
    animation: animScaleQut 1s ease-in-out infinite;
    animation-iteration-count: 1;
    opacity: 1;
}

@keyframes animScaleQut {
    0% {
        transform: translateY(-35%) scale(0);
    }
    50% {
        transform: translateY(-35%) scale(1.5);
    }
    100% {
        transform: translateY(-35%) scale(1);
    }
}

.last-word {
    position: relative;
    &::after {
        content: url(../img/main/svg/quotes.svg);
        position: absolute;
        top: 50%;
        transform: translateY(-35%) scale(1);
        right: -45px;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: -1;
        opacity: 0;
        transition: $animTransition;
    }
}
.slogan-title-text {
    @include segeoRegular;
    font-size: 24px;
    line-height: 40px;
    color: #000;
}
.video-title-caption {
    @include segeoRegular;
    font-size: 60px;
    line-height: 40px;
    color: #000;
}
.video__block {
    text-align: center;
    margin-top: 86px;
}
.video__block-wrapper {
    position: relative;
    margin-top: 63px;
    transition: all 0.3s ease 0s ;
    &:hover {
        box-shadow: 0px 0px 20px 10px rgba(226, 20, 229, 0.25);
    }
}
.video {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.video__item-box {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    transition: .3s;
    transition-property: transform, opacity;
    user-select: none;
    .img-preview {
        width: 100%;
        height: 100%;
    }
    &:hover {
        transition: .3s;
        transition-property: transform, opacity;
        box-shadow: 0px 0px 30px 3px rgba(1,4,28,1);
    }
}
.video__item {
    padding-top: 56.25%; // 16:9 ( (9/16)*100 ) x=56.25%
}

@media (max-width:1024px) {
    .section__video {
        padding: 45px 0 0 0 !important;
    }
}

@media (max-width:1100px) {
    .slogan-title-caption {
        font-size: 36px;
    }
}

@media (max-width: 1024px) {
    .video__slogan {
        max-width: 700px;
        margin: 0 auto;
    } 
    .slogan-title-caption {
        line-height: 60px !important;
    }
}
@media (max-width: 790px) {
    .video__slogan {
        max-width: 500px;
    }
}
@media (max-width: 590px) {
    .video-title-caption {
        font-size: 40px;
    }
    .video__block {
        margin-top: 35px;
    }
}

@media (max-width: 425px) {
    .section__video {
        padding: 35px 0 0 0 !important;
    }
    .video__slogan {
        max-width: 300px;
    }
    .video-title-caption {
        font-size: 36px;
    }
    .video__block-wrapper {
        margin-top: 35px;
    }
    .slogan-title-caption {
        font-size: 30px !important;
        line-height: 45px !important;
    }
}

@media(max-width:375px) {
    .video-title-caption {
        font-size: 30px;
    }
}

@media(max-width:320px) {
    .first-word::before {
        left: -35px;
    }
    .last-word::after {
        right: -35px;
    }
}