// For <= 1980px
@media screen and (min-width: 1980px) {
    .section__present {
        height: calc(100vh - 250px);
    }
    .present__container {
        width: 100% !important;
        padding: 0 480px !important;
    }
    .header__menu {
        max-width: 2000px;
    }
    .header__menu-item {
        font-size: calc(24px + (2 + 2 * 0.7) * (100vw - 320px) / 1524);
    }  
    .logo {
        width: 500px !important;
    } 
    .present__slogan p {
        font-size: 36px !important;
    }
    .video__container {
        max-width: 2000px !important;
    }
    .container__main {
        max-width: 2000px !important;
    }
    .left-title-caption {
        font-size: 80px !important;
    }
    .section__ideaOne .content__idea-left {
        max-width: 750px !important;
    }
    .slogan-title-caption {
        font-size: 70px !important;
        margin-bottom: 30px !important;
    }
    .slogan-title-text {
        font-size: 36px !important;
    }
    .idea__text {
        max-width: 850px !important;
    }
    .idea__text-desc {
        font-size: 36px !important;
        line-height: 50px !important;
    }
    .idea__img-box {
        width: 750px !important;
        height: 750px !important;
    }
    .number__box {
        width: 450px !important;
        height: 450px !important;
        font-size: 120px !important;
    }
    .line,
    .line__bottom,
    .line__top {
        border-top: 8px dotted #7DE3FC !important;
    }
    .content__idea-left {
        max-width: 800px !important;
    }
    .idea__data span {
        font-size: 150px !important;
        line-height: 160px !important;
    }
    .specific-list-item {
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
    }
    .idea__data-text {
        font-size: 36px !important;
    }
    .specific-title {
        font-size: 80px !important;
        line-height: 90px !important;
    }
    .specific-list-item {
        font-size: 36px !important;
        line-height: 60px !important;
    }
    .idea__title-caption {
        font-size: 80px !important;
    }
    .controll__list-item {
        font-size: 36px !important;
    }
    .controll-icon {
        width: 90px;
    }
    .section__ideaObject .line__bottom {
        width: 140% !important;
        bottom: -70% !important;
    }
    .section__ideaObject .content__idea-right  {
        bottom: 15% !important;
    }
    .tabs__wrapper {
        max-width: 2200px !important;
    }
    .section__ideaGame .content__idea-left {
        margin-top: 600px;
    }
    .section__ideaObject {
        padding: 150px 0 180px 0 !important;
    }
    .section__ideaObject .content__idea-right {
        margin-top: 500px !important;
    }
    .footer__copyright {
        font-size: 36px;
    }
}
// /For <= 1980px