.section__idea.section__ideaGame {
    padding: 0;
}
.section__ideaGame .content__idea-left {
    align-self: flex-end;
    padding: 80px 0;
    
}
.section__ideaGame .content__idea-right {
    align-self: flex-start;
    padding: 40px 0;
}
.section__ideaGame .number__box {
    top: 32%;
}
.number__box::before {
    z-index: -1;
}
.number__box::after {
    z-index: -1;
}
.section__ideaGame .idea__title {
    position: absolute;
    top: 35%;
    left: 25%
}
.section__ideaGame .idea__text {
    max-width: 550px;
}

.section__ideaGame .left-title-caption {
    transform: translate(-250%, 0);
    opacity: 0;
    transition: $animTransition;
    &._active {
        transform: translate(0, 0);
        opacity: 1;
    }
}

.section__ideaGame .idea__text-desc {
    transform: translate(-120%, 0);
    opacity: 0;
    transition: $animTransition;
}
.section__ideaGame .idea__text._active .idea__text-desc {
    transform: translate(0, 0);
    opacity: 1;
    transition: $animTransition;
}

@media(max-width:1980px) {
    .section__ideaGame .content__idea-left {
        margin-top: 400px;
    }
}

@media(max-width:1220px) {
    .section__ideaGame .idea__title {
        left: 20%;
    }
    .section__ideaGame .idea__text {
        max-width: 430px;
    }
}

@media (max-width: 1024px) {
    .section__ideaGame .idea__title {
        position: static;
        margin-bottom: 35px;
    }
}
@media (max-width: 980px) {
    .section__ideaGame .idea__text {
        max-width: 100%;
    }
    .section__ideaGame {
        padding-bottom: 50px !important;
        height: auto;
    }
    .section__ideaGame .content__idea-left {
        order: 1;
        padding: 0;
        margin: 0;
        align-self: center;
    }
    .section__ideaGame .content__idea-right {
        align-self: flex-end;
        transform: translateX(calc(-100vw + 80%));
    }
    .number__box {
        z-index: 98;
        &::before {
            display: none;
        }
         &::after {
            display: none;
        }
    }
}
@media (max-width: 768px) {
    .section__ideaGame .number__box {
        top: 28%;
        left: 90%;
    }
}
@media (max-width: 425px) {
    .section__ideaGame .number__box {
        top: 24%;
    }
    .left-title-caption {
        font-size: 50px;
    }
}