.alert__game {
    display: none;
    opacity: 0;
    transition: .4s;
    position: fixed;
    top: 5%;
    left: 50%;
    width: 90%;
    transform: translate(-50%, 0);  
    padding: 20px;
    border-radius: 15px;
    transition: all 0.3s ease 0s;
    background-color: rgba(203, 222, 31, 0.95);
    border: 1px solid #a3b405f2;
    box-shadow: 0px 0px 23px 4px rgba(200, 212, 8, 0.25);
    z-index: 101;
    &-content {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
.alert__game-text {
    @include segeoRegular;
    font-size: 16px;
    line-height: 20px;
    color: #000;
}
.alert__game-close {
    position: relative;
    display: block;
    width: 12px;
    height: 12px;
    cursor: pointer;
    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: #000;
        top: 50%;
        left: 0;
        transform: translate(0, -50%) rotate(45deg);
    }
    &::after {
        content: '';
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: #000;
        left: 0;
        bottom: 50%;
        transform: translate(0, 50%) rotate(-45deg);
    }
}
.show-alert {
    transition: all 0.3s ease 0s;
    display: block;
    animation: showAlert 0.3s ease-in-out infinite;
    animation-iteration-count: 1;
    opacity: 1;
}
@keyframes showAlert {
    0% {
        opacity: 0;
    }
    25% {
        opacity: 0.25;
    }
    50% {
        opacity: 0.5;
    }
     75% {
        opacity: 0.75;
    }
    100% {
        opacity: 1;
    }
}

@media (max-width: 660px) {
    .alert__game-text {
        font-size: 14px;
        line-height: 18px;
    }
}
@media (max-width: 590px) {
    .alert__game {
        top: 10%;
        width: 95%;
    }
    .alert__game-text {
        font-size: 12px;
        line-height: 18px;
        padding-right: 8px;
    }
}
@media (max-width: 375px) {
    .alert__game-close {
        width: 15px;
        height: 15px;
    }
}