.section__ideaTwo .line {
    position: absolute;
    left: -25%;
    top: 800px;
    margin: 0;
    border: none;
    border-top: 6px dotted #7DE3FC;
    color: #7DE3FC;
    background-color: transparent;
    height: 0;
    width: 150%;
    transform: rotate(90deg);
}

.section__ideaTwo .idea__text-desc {
    transform: translate(120%, 0);
    opacity: 0;
    transition: $animTransition;
    &._active {
        transform: translate(0, 0);
        opacity: 1;
    }
}

@media (max-width: 1220px) {
    .section__ideaTwo .idea__text {
        max-width: 400px;
    }
}

@media (max-width: 1230px) {
    .line {
        border-top: 5px dotted #7DE3FC !important;
    }
}

@media (max-width: 980px) {
    .section__ideaTwo .content__idea {
        align-items: flex-start;
    }
    .section__ideaTwo .content__idea-left {
        transform: translateX(calc(100vw - 80%));
    }
    .section__ideaTwo .content__idea-right {
        max-width: 600px;
        margin: 0 auto;
    }
    .section__ideaTwo .idea__text {
        padding-top: 30px;
    }
    .section__ideaTwo .idea__text {
        max-width: 100%;
    }
}