.line__top {
    position: absolute;
    margin: 0;
    border:none;
    border-top: 6px dotted #7DE3FC;
    color:#7DE3FC;
    background-color: transparent;
    height:0;
    width: 200%;
    transform: rotate(90deg);
    top: -100%;  
}
.line__bottom {
    position: absolute;
    margin: 0;
    border:none;
    border-top: 6px dotted #7DE3FC;
    color:#7DE3FC;
    background-color: transparent;
    height:0;
    width: 300%;
    transform: rotate(90deg);
    bottom: -150%;  
}
.section__idea {
    overflow: hidden;
    padding: 30px 0 30px 0;
}
.container__main {
    position: relative;
    max-width: 1350px;
    margin: 0 auto;
    padding: 0 30px;
    height: 100%;
}
.content__idea {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}
.content__idea-left {
    max-width: 600px;
}
.idea__title {
    margin-bottom: 77px;
}

.left-title-caption {
    @include segeoRegular;
    font-size: 60px;
    line-height: 40px;
    color: #000;
}

.section__ideaOne .left-title-caption {
    // anim styles
    transform: translate(-120%, 0);
    opacity: 0;
    transition: $animTransition;
}
.section__ideaOne .left-title-caption._active {
    transform: translate(0, 0);
    opacity: 1;
}

.idea__text {
    max-width: 440px;
    shape-outside: circle(50%);
    z-index: 99;
    &-desc {
        @include segeoRegular;
        font-size: 24px;
        line-height: 30px;
        color: #000;
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: 0;
        }
        span {
            @include segeoBold;
            color: $brandColor;
        }
    }
}
.section__ideaOne .idea__text-desc {
    transform: translate(-120%, 0);
    opacity: 0;
    transition: $animTransition;

    &._active{
        transform: translate(0, 0);
        opacity: 1;
    }
    @for $i from 1 through 2 {
        &._active:nth-child(#{$i}) {
            transition-delay: 0.2s * $i;
        }
    }
}

.idea__data span {
    @include segeoBold;
    font-size: 100px;
    line-height: 70px;
    color: $brandColor;
}
.idea__data-text {
    @include segeoRegular;
    font-size: 24px;
    line-height: 70px;
    color: #000;
}
.idea__specific-title {
    margin-bottom: 28px;
}
.specific-title {
    @include segeoRegular;
    font-size: 60px;
    line-height: 70px;
    color: $brandColor;
}
.specific-list-item {
    @include segeoRegular;
    font-size: 24px;
    line-height: 46px;
    color: #000;
    span {
       @include segeoBold; 
       color: $brandColor;
    }
}
.idea__img-box {
    width: 500px;
    height: 500px;
    z-index: -1;
    user-select: none;
}
.idea__img {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0; top: 0; bottom: 0; right: 0;
}
.idea__img-shadow {
    transform: scale(1.7) translateZ(-10vmin);
}
.number__box {
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate(-50%, -50%);
    background: url(../img/main/circle.png) no-repeat center;
    background-size: cover;
    // border: 6px dotted #7DE3FC;
    border-radius: 50%;
    width: 330px;
    height: 330px;
    @include segeoRegular;
    font-size: 100px;
    line-height: 30px;
    color: #000;
    user-select: none;
}

@media (max-width: 1024px) {
    .section__ideaOne .content__idea-right {
        align-self: flex-start;
    }
}

@media(max-width: 1220px) {
    .section__ideaTwo .line {
        top: 600px !important;
    }
    .idea__text-desc {
        font-size: 19px !important;
    }
    .idea__img-box {
        width: 400px;
        height: 400px;
    }
}


@media (max-width: 980px) {
    .content__idea {
        background: none !important;
        overflow: inherit !important;
    }
    .section__ideaGame.section__idea {
        padding: 170px 0 20px 0;
        margin-top: -150px;
    }
    .section__ideaOne .number__box {
        top: 30%;
    }
    .line {
        display: none;
    }
    .line__bottom {
        display: none;
    }
    .line__top {
        display: none;
    }
    .tabs__wrapper::after {
        display: none;
    }
    .section__idea {
        height: auto;
    }
    .content__idea {
        flex-direction: column;
    }
    .section__ideaOne .content__idea-left {
        order: 1;
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
    .section__ideaOne .content__idea-right {
        transform: translateX(calc(100vw - 80%));
    }
    .content__idea-right {
        padding-bottom: 0;
    }
    .idea__text {
        max-width: 100%;
        z-index: 99;
    }
}

@media (max-width: 1230px) {
    .section__ideaOne .content__idea-left {
        max-width: 320px;
    }
    .number__box {
        width: 300px;
        height: 300px;
    }
    .line__bottom {
        border-top: 5px dotted #7DE3FC;
    }
    .line__top {
        border-top: 5px dotted #7DE3FC;
    }
}

@media (max-width: 980px) {
    .section__ideaOne .content__idea-left {
        max-width: 600px;
    }
    .number__box {
        width: 300px;
        height: 300px;
    }
    .line__bottom {
        border-top: 5px dotted #7DE3FC;
    }
    .line__top {
        border-top: 5px dotted #7DE3FC;
    }
}

@media(max-width:1125px) {
    .idea__data span {
        font-size: 80px;
    }
}

@media (max-width: 768px) {
    .number__box {
        top: 30%;
        left: 15%;
        z-index: 99;
    }
    .section__ideaOne .content__idea-left {
        z-index: 99;
    }
}

@media (max-width: 530px) {
    .container__main {
        padding: 0 20px;
    }
    .idea__img-box {
        width: 400px;
        height: 400px;
    }
}

@media (max-width: 425px) {
    .number__box {
        width: 250px;
        height: 250px;
    }
    .idea__img-box {
        width: 330px;
        height: 330px;
    }
}

@media (max-width: 375px) {
    .number__box {
        top: 25%;
    }
    .idea__text-desc {
        line-height: 25px;
    }
    .idea__title {
        margin-bottom: 40px;
    }
}

@media (max-width: 2560px) and (min-width: 589px) {
    .section__ideaOne .content__idea-left {
        z-index: 99;
    }
    .section__ideaTrees .content__idea-right {
        z-index: 99;
    }
}