.section__ideaSpecific .line {
    position: absolute;
    margin: 0;
    left: -25%;
    border: none;
    border-top: 6px dotted #7DE3FC;
    color: #7DE3FC;
    background-color: transparent;
    height: 0;
    width: 150%;
    transform: rotate(90deg);
}


.idea__data span {
    line-height: 80px;
}
.section__ideaSpecific .idea__data span, .idea__data-text {
    display: block;
    transform: translate(0, 120%);
    opacity: 0;
    transition: $animTransition;
    &._active{
        transform: translate(0, 0);
        opacity: 1;
        @for $i from 1 through 2 {
            &:nth-child(#{$i}) {
                transition-delay: .2s * $i;
            }
        }
    }
}

.specific-list .specific-list-item {
    transform: translate(-120%, 0);
    opacity: 0;
    transition: $animTransition;
}

.specific-list._active .specific-list-item {
    transform: translate(0, 0);
    opacity: 1;
    transition: $animTransition;
    @for $i from 1 through 5 {
        &:nth-child(#{$i}) {
            transition-delay: .2s * $i;
        }
    }
}

@media (max-width: 1220px) {
    .specific-title {
        font-size: 40px;
        line-height: 50px;
    }
    .specific-list-item {
        font-size: 18px;
    }
    .idea__data span {
        font-size: 80px;
    }
    .idea__data-text {
        font-size: 19px !important;
    }
}

@media (max-width: 1200px) {
    .specific-title {
        font-size: 50px;
    }
}

@media (max-width: 1024px) {
    .section__ideaSpecific .content__idea-left{
        z-index: 99;
    }
}

@media (max-width: 980px) {
    .section__ideaSpecific .content__idea {
        align-items: flex-start;
    }
    .section__ideaSpecific .content__idea-left{
        order: 1;
        width: 100%;
        align-self: center;
    }
    .section__ideaSpecific .content__idea-right{
        transform: translateX(calc(100vw - 80%));
    }}
@media (max-width: 560px) {
    .idea__data span {
        font-size: 60px;
    }
    .specific-title {
        font-size: 30px !important;
        line-height: 40px !important;
    }
    .specific-list-item {
        font-size: 20px;
    }
}

@media (max-width: 425px) {
    .idea__specific {
        margin-top: 20px;
    }
    .idea__data span {
        font-size: 60px;
    }
    .idea__data-text {
        line-height: 30px;
    }
    .specific-list-item {
        font-size: 16px;
    }
}

@media (max-width: 375px) {
    .idea__data span {
        font-size: 50px;
    }
    .specific-list-item {
        display: flex;
        flex-direction: column;
        line-height: 25px;
        margin-bottom: 10px;
        &:last-child {
            margin-bottom: 0;
        }
    }
}