.section__game {
    height: 100vh;
    background: url(../img/game/game-preview.png) no-repeat center;
    background-size: cover;
    overflow: hidden;
}
.container__game {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(../img/game/game-circle.png) no-repeat center;
    background-size: contain;
}

.game__play {
    transform: rotate(45deg);
}

.game__play-btn {
    width: 330px;
    height: 330px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $brandColor;
    animation-name: pulse;
    animation-duration: 2s;
    animation-iteration-count:infinite;
    span {
        transform: rotate(-45deg);
        @include segeoRegular;
        font-size: 40px;
        line-height: 70px;
        color: #000;
    }
}

@keyframes pulse {
  0% {
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  box-shadow: 0px 0px 0px 0px rgba(6,199,244,0.7);
  }
  50% {
  -webkit-transform: scale3d(1.05, 1.05, 1.05);
  transform: scale3d(1.05, 1.05, 1.05);
  box-shadow: 0px 0px 20px 5px rgba(6,199,244,0.7);
  }
  100% {
  -webkit-transform: scale3d(1, 1, 1);
  transform: scale3d(1, 1, 1);
  box-shadow: 0px 0px 0px 0px rgba(6,199,244,0.7);
  }
} 

@media (max-width: 1024px and min-width 980px) {
    .section__ideaGame .content__idea {
        overflow: hidden;
    }
}

@media (max-width: 690px) {
    .container__game {
        background-size: cover;
    }
    .game__play-btn {
        width: 300px;
        height: 300px;
    }
}
@media (max-width: 425px) {
    .game__play-btn {
        width: 200px;
        height: 200px;
    }
    .game__play-btn span {
        font-size: 24px;
    }
}