.tabs__wrapper {
    position: relative;
    max-width: 1600px;
    margin: 0 auto;
    padding: 50px 20px;
    width: 100%;
    &::after {
        content: " ";
        background-image: url(../img/main/svg/line-5.svg);
        height: 260px; width: 50px;
        background-size: contain;
        background-repeat: no-repeat;
        position: absolute;
        background-position: center top;
        left: 50%;
        transform: translateX(-50%);
    }
}
.section__tabs {
    width: 100%;
    height: 100vh;
}