.section__ideaTrees .section__idea {
    padding: 0 !important;
}
.section__ideaTrees .idea__title {
    position: absolute;
    top: 20%;
    left: 20%;
    margin: 0;
}
.sectionIdeaTrees .content__idea {
    overflow: auto !important;
}
.section__ideaTrees .content__idea-right {
    height: 100%;
    align-items: center;
    padding-bottom: 40px;
    margin-top:400px;
}
.section__ideaTrees .idea__title-caption {
    @include segeoRegular;
    font-size: 60px;
    line-height: 70px;
    text-align: end;
    transform: translate(-200%, 0);
    opacity: 0;
    transition: $animTransition;
    &._active {
        transform: translate(0, 0);
        opacity: 1;
    }
}
.section__ideaTrees .idea__text {
    max-width: 550px;
}

.section__ideaTrees .idea__text .idea__text-desc{
    transform: translate(120%, 0);
    opacity: 0;
    transition: $animTransition;
}
.section__ideaTrees .idea__text._active .idea__text-desc {
    transform: translate(0, 0);
    opacity: 1;
    transition: $animTransition;
    @for $i from 1 through 3 {
        &:nth-child(#{$i}) {
            transition-delay: .2s * $i;
        }
    }
}
.section__ideaTrees{
    margin-top: 0 !important;
}

.section__ideaTrees .number__box {
    top: 20%;
}

@media(max-width: 1290px) {
    .section__ideaTrees .idea__title {
        left: 12%;
    }
}
@media(max-width: 1190px) {
    .section__ideaTrees .idea__text {
        max-width: 450px;
    }
}
@media (max-width: 980px) {
    .section__ideaTrees .idea__text {
        max-width: 550px;
    }
    .section__ideaTrees .idea__title {
        position: static;
        width: 100%;
        margin-bottom: 35px;
        z-index: 99;
    }
    .section__ideaTrees .idea__title-caption {
        text-align: left;
    }
    .section__ideaTrees .number__box {
        top: 20%;
    }

    .section__ideaTrees .content__idea-right{
        display: flex;
        flex-direction: column;
    }
}

@media(max-width:590px) {
    .section__ideaTrees .content__idea-right {
        margin-top: 280px;
    }
}

@media (max-width: 425px) {
    .section__ideaTrees .idea__title-caption {
        font-size: 50px;
    }
}

@media (max-width: 375px) {
    .idea__title-caption {
        font-size: 40px !important;
        line-height: 50px !important;
    }
}