.section__present {
    position: relative;
    height: calc(100vh - 250px);
    display: flex;
    align-items: center;
    overflow: hidden;
}
.section__present::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: #030c21;
    z-index: -5;
}
.present__container {
    width: 1100px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 0 20px;
}
.logo {
    position: relative;
    width: 350px;
    height: 250px;
    background: url(../img/main/shadow.png) no-repeat center;
    background-size: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    transform: scale(0);
    opacity: 0;
    transition: $animTransition;
    .logo-img {
        width: 100%;
        object-fit: contain;
        object-position: center;
    }
    &._active {
        transform: scale(1);
        opacity: 1;
        transition-delay: 0.4s;
    }
}

.logo-link {
    width: 100%;
    text-align: center;
}
.present__slogan {
    position: relative;
}
.present__slogan-text {
    font-size: 30px;
    color: #fff;
    @include segeoRegular;
    & span {
        display: block;
        margin-top: 15px;
        transform: translate(150%, 0);
        opacity: 0;
        transition: $animTransition;
        &:first-child {
            margin-top: 0;
        }
    }
}

.present__slogan-text._active span {
    transform: translate(0, 0);
    opacity: 1;

    transition: $animTransition;
    @for $i from 1 through 3 {
        &:nth-child(#{$i}) {
            transition-delay: .2s * $i;
        }
    }
}


.present__slogan-about {
    position: absolute;
    bottom: -100px;
    font-size: 24px;
    color: #fff;
    line-height: 40px;
    @include segeoRegular;
    transform: translate(0, 120%);
    opacity: 0;
    transition: $animTransition;
    &._active {
       transform: translate(0, 0);
        opacity: 1; 
        transition-delay: 1s;
    }
}
.container-canvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    .p5Canvas {
        width: 100%;
        height: 100%;
    }
}

@media (max-width: 1400px) {
    .container__main {
        padding: 0 60px;
    }
}

@media (max-width: 1200px) {
    .present__container {
        padding: 0 80px;
    }
}

@media (max-width: 790px) {
    .present__slogan-text span {
        margin-top: 10px;
    }
    .present__container {
        height: 100%;
        flex-wrap: wrap;
    }
    .present__slogan-about {
        position: static;
        transform: translateY(0);
        margin-top: 30px;
    }
}

@media (max-width: 900px) {
    .logo {
        padding: 20px;
    }
}

@media(max-width: 425px) {
    .section__present {
        height: calc(100vh - 200px);
    }
    .present__slogan-text span {
        margin-top: 0;
    }
    .present__container {
        height: 100%;
    }
    .logo {
        width: 260px;
        height: 200px;
        margin-top: 20px;
        background-size: 100%;
    }
    .logo .logo-img {
        width: 80%;
    }
    .present__slogan {
        text-align: center;
    }
    .present__slogan-text {
        line-height: 30px;
    }
}

@media (max-height:600px), (max-height:750px) and (max-width:790px) { // PIECE OF SHIT
    .section__present {
        height: 100vh;
    }
    .logo {
        margin-top: 30px;
        width: 280px;
        height: 180px;
    }
    .present__slogan-text {
        font-size: 20px !important;
    }
    .present__slogan-about {
        font-size: 16px !important;
    }
    .header__menu-item {
        font-size: 16px;
    }
}

@media(max-height:425px) {
    .present__slogan-about {
        bottom: -80px;    
    }
    .section__present {
        min-height: 100%;
    }
    .present__container {
        height: 100%;
    }
    .logo {
        width: 250px;
        height: 180px;
    }
}
