.section__idea.section__ideaObject {
    padding: 0 0 50px 0;
}
.section__ideaObject .content__idea-left {
    align-self: flex-start;
    padding: 40px 0 0 0;
}
.section__ideaObject .content__idea-right {
    display: block;
    align-self: flex-end;
    margin-top: 380px;
    z-index: 98;
}

.section__ideaObject .left-title-caption {
    transform: translate(120%, 0);
    opacity: 0;
    transition: $animTransition;
    &._active {
        transform: translate(0, 0);
        opacity: 1;
    }
}

.section__ideaObject .idea__text-desc {
    transform: translate(120%, 0);
    opacity: 0;
    transition: $animTransition;
}
.section__ideaObject .idea__text._active .idea__text-desc {
    transform: translate(0, 0);
    opacity: 1;
    transition: $animTransition;
}


.controll__list .controll__list-item {
    transform: translate(0, 120%);
    opacity: 0;
    transition: $animTransition;
    overflow: hidden;
}

.controll__list._active .controll__list-item {
    transform: translate(0, 0);
    opacity: 1;

    transition: $animTransition;
    @for $i from 1 through 5 {
        &:nth-child(#{$i}) {
            transition-delay: .2s * $i;
        }
    }
}

.section__ideaObject .idea__text {
    max-width: 550px;
}

.section__ideaObject .number__box {
    top: 32%;
}
.controll__list {
    margin-top: 83px;
}
.controll__list-item {
    @include segeoRegular;
    font-size: 24px;
    line-height: 30px;
    color: #000;
    display: flex;
    align-items: center;
    margin-bottom: 25px;
    &:last-child {
        margin-bottom: 0;
    }
}
.controll-icon {
    object-fit: cover;
    object-position: center;
    margin-right: 10px;
}
.section__ideaObject .line__bottom {
    width: 100%;
    bottom: -50%;
    &::after {
        position: absolute;
        content: '';
        width: 15px;
        height: 15px;
        border: 6px solid #7DE3FC;
        border-radius: 50%;
        right: -21px;
        bottom: 50%;
        transform: translateY(40%);
    }
}

@media (max-width: 1220px) {
    .controll__list-item {
        font-size: 18px;
    }
    .controll-icon {
        width: 50px;
    }
    .section__ideaObject .content__idea-right {
        bottom: 0;
        max-width: 430px;
    }
}

@media (max-width: 1200px) {
    .idea__title {
        margin-bottom: 50px;
    }
    .controll__list {
        margin-top: 50px;
    }
}
@media (max-width: 980px) {
    .section__ideaObject .content__idea-right {
        position: static;
        align-self: center;
        z-index: 99;
        margin: 0;
        max-width: 100%;
    }
    .section__ideaObject .content__idea-left {
        transform: translateX(calc(100vw - 80%));
    }
    .section__ideaObject {
        height: auto;
    }
    .section__ideaObject .number__box {
        top: 25%;
    }
}

@media (max-width: 560px) {
    .controll__list-item {
        font-size: 18px;
    }
    .controll-icon {
        width: 50px;
    }
    .section__idea.section__ideaObject {
        padding: 0;
    }
}

@media (max-width: 375px) {
    .controll__list-item {
        font-size: 16px !important;
    }
    .controll-icon {
        width: 40px !important;
    }
}