.section__future {
    padding-top: 50px;
    height: 100vh;
}
.future__content {
    width: 100%;
    height: 100%;
}
.future__quote {
    @include segeoRegular;
    text-align: center;
    font-size: 40px;
    line-height: 60px;
    padding-top: 270px;
    
}
.future__quote-text {
    display: block;
    transform: scale(0);
    opacity: 0;
    transition: $animTransition;
    &._active {
        transform: scale(1);
        opacity: 1;
    }
}
.future__banner {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url(../img/main/future-poster.png) no-repeat center;
    background-size: cover;
}
.future__banner-caption  {
    text-align: center;
    @include segeoBold;
    font-size: 100px;
    line-height: 80px;
    color: #fff;
    transform: scaleX(0);
    opacity: 0;
    transition: $animTransition;
    &._active {
        transform: scaleX(1);
        opacity: 1;   
    }
}

@media (max-width: 980px) {
    .future__quote {
        padding-top: 0;
    }
    .future__quote-text {
        font-size: 30px;
    }
}

@media (max-width: 769px) {
    .future__quote {
        width: 80%;
        margin: 0 auto;
        line-height: 50px;
    }
    .future__banner-caption {
        font-size: 60px;
    }
}

@media (max-width: 425px) {
    .future__banner-caption {
        font-size: 40px;
        line-height: 50px;
    }
    .future__quote {
        width: 85%;
    }
}