.footer {
    background: #040D21;
    padding: 90px 0;
}
.footer__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.footer__copyright {
    @include segeoRegular;
    font-size: 24px;
    line-height: 60px;
    color: #fff;
    display: block;
}
.footer__company {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &-item {
        margin-left: 60px;
        user-select: none;
        transform: scale(0);
        opacity: 0;
        transition: $animTransition;
        &:first-child {
            margin-left: 0;
        }
    }
}
.footer__company._active .footer__company-item {
    transform: scale(1);
    opacity: 1;
    transition: $animTransition;
    @for $i from 1 through 3 {
        &:nth-child(#{$i}) {
            transition-delay: .2s * $i;
        }
    }
}
.microsoft {
    width: 250px;
    height: 80px;
}
.awara {
    width: 220px;
    height: 50px;
}
.company-item-img {
    width: 100%;
    height: 100%;
}

@media (max-width: 1250px) {
    .footer__content {
        flex-direction: column;
    }
    .footer__copyright {
        margin-bottom: 25px;
    }
}

@media (max-width: 930px) {
    .footer__company-item {
        width: 200px;
    }
}
@media (max-width: 768px) {
    .footer__company-item {
        margin-left: 20px;
    }
    .footer {
        padding: 50px 0;
    }
}
@media (max-width: 660px) {
    .footer {
        padding: 30px 0;
    }
    .footer__company {
        width: 100%;
        justify-content: space-around;
        align-items: center;
        &-item {
            margin-left: 0;
            width: 150px;
            height: 50px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}
@media (max-width: 425px) {
    .footer {
        padding: 20px 0;
    }
    .footer__copyright {
        font-size: 16px;
        margin-bottom: 0;
    }
    .footer__company-item {
        margin-left: 35px;
        &:first-child {
            margin-left: 0;
        }
    }
}
@media (max-width: 375px) {
    .footer {
        padding: 10px 0;
    }
    .footer__copyright {
        font-size: 16px;
    }
    .footer__company-item {
        margin-left: 15px;
    }
}
